'use client';

import { useState, useRef, useEffect, use } from 'react';
import { getPackageById, updatePackage } from '@/actions/package';
import { useRouter } from 'next/navigation';

export default function EditPackagePage({ params }: { params: Promise<{ id: string }> }) {
  const { id } = use(params);
  const router = useRouter();
  const fileRef = useRef<HTMLInputElement>(null);

  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [price, setPrice] = useState('');
  const [category, setCategory] = useState('');
  const [stock, setStock] = useState(0);
  const [sortOrder, setSortOrder] = useState(0);
  const [isActive, setIsActive] = useState(true);
  const [imageUrl, setImageUrl] = useState('');
  const [imagePreview, setImagePreview] = useState('');
  const [uploading, setUploading] = useState(false);
  const [saving, setSaving] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    getPackageById(id).then(pkg => {
      if (!pkg) { router.push('/admin/packages'); return; }
      setName(pkg.name);
      setDescription(pkg.description || '');
      setPrice(pkg.price);
      setCategory(pkg.category);
      setStock(pkg.stock);
      setSortOrder(pkg.sortOrder);
      setIsActive(pkg.isActive);
      setImageUrl(pkg.imageUrl || '');
      setImagePreview(pkg.imageUrl || '');
      setLoading(false);
    }).catch(() => {
      setError('Gagal memuat data paket.');
      setLoading(false);
    });
  }, [id, router]);

  const handleImageChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    const localPreview = URL.createObjectURL(file);
    setImagePreview(localPreview);
    setUploading(true);
    setError('');
    const fd = new FormData();
    fd.append('file', file);
    try {
      const res = await fetch('/api/upload', { method: 'POST', body: fd });
      const data = await res.json();
      if (data.success && data.url) {
        setImageUrl(data.url);
      } else {
        setError(data.error || 'Upload gagal');
        setImagePreview(imageUrl);
      }
    } catch {
      setError('Upload gagal. Coba lagi.');
      setImagePreview(imageUrl);
    } finally {
      setUploading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!name || !price || !category) {
      setError('Nama, harga, dan kategori wajib diisi.');
      return;
    }
    setSaving(true);
    setError('');
    const res = await updatePackage(id, { name, description, price, category, imageUrl, stock, isActive, sortOrder });
    if (res.success) {
      router.push('/admin/packages');
    } else {
      setError(res.error || 'Gagal menyimpan paket.');
      setSaving(false);
    }
  };

  if (loading) return (
    <div className="flex items-center justify-center py-24">
      <div className="animate-spin w-8 h-8 border-2 border-blue-500 border-t-transparent rounded-full"></div>
    </div>
  );

  return (
    <div className="max-w-2xl">
      <header className="mb-8 border-b border-neutral-800 pb-5">
        <h1 className="text-3xl font-bold">Edit Paket</h1>
        <p className="text-neutral-400 mt-1">Perbarui informasi paket gift skin.</p>
      </header>

      {error && (
        <div className="mb-6 p-4 bg-red-500/10 border border-red-500/20 text-red-400 rounded-xl text-sm flex items-center gap-2">
          <i className="fa-solid fa-circle-exclamation"></i> {error}
        </div>
      )}

      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Image Upload */}
        <div className="space-y-2">
          <label className="block text-sm font-semibold text-neutral-300">Gambar Paket</label>
          <div
            className="relative h-48 bg-neutral-900 border-2 border-dashed border-neutral-700 hover:border-blue-500/50 rounded-xl cursor-pointer transition-colors overflow-hidden"
            onClick={() => fileRef.current?.click()}
          >
            {imagePreview ? (
              // eslint-disable-next-line @next/next/no-img-element
              <img src={imagePreview} alt="preview" className="w-full h-full object-cover" />
            ) : (
              <div className="flex flex-col items-center justify-center h-full gap-2 text-neutral-500">
                <i className="fa-solid fa-cloud-arrow-up text-3xl"></i>
                <span className="text-sm font-medium">Klik untuk ganti gambar</span>
              </div>
            )}
            {uploading && (
              <div className="absolute inset-0 bg-black/60 flex items-center justify-center">
                <div className="animate-spin w-8 h-8 border-2 border-blue-500 border-t-transparent rounded-full"></div>
              </div>
            )}
          </div>
          <input ref={fileRef} type="file" accept="image/*" className="hidden" onChange={handleImageChange} />
          {imageUrl && !uploading && (
            <p className="text-xs text-emerald-400"><i className="fa-solid fa-check mr-1"></i>Gambar sudah diset</p>
          )}
        </div>

        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
          <div className="space-y-2">
            <label className="block text-sm font-semibold text-neutral-300">Nama Paket *</label>
            <input
              type="text" value={name} onChange={e => setName(e.target.value)}
              className="w-full bg-neutral-900 border border-neutral-700 rounded-xl p-3.5 text-white focus:border-blue-500 focus:ring-1 focus:ring-blue-500 transition-all outline-none"
            />
          </div>
          <div className="space-y-2">
            <label className="block text-sm font-semibold text-neutral-300">Kategori *</label>
            <select
              value={category} onChange={e => setCategory(e.target.value)}
              className="w-full bg-neutral-900 border border-neutral-700 rounded-xl p-3.5 text-white focus:border-blue-500 transition-all outline-none appearance-none"
            >
              <option value="">Pilih Kategori...</option>
              <option value="Basic">Basic</option>
              <option value="Premium">Premium</option>
              <option value="Special">Special</option>
              <option value="Event">Event</option>
              <option value="Limited">Limited</option>
            </select>
          </div>
        </div>

        <div className="space-y-2">
          <label className="block text-sm font-semibold text-neutral-300">Deskripsi</label>
          <textarea
            value={description} onChange={e => setDescription(e.target.value)}
            rows={3}
            className="w-full bg-neutral-900 border border-neutral-700 rounded-xl p-3.5 text-white focus:border-blue-500 focus:ring-1 focus:ring-blue-500 transition-all outline-none resize-none"
          />
        </div>

        <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
          <div className="space-y-2 sm:col-span-2">
            <label className="block text-sm font-semibold text-neutral-300">Harga *</label>
            <input
              type="text" value={price} onChange={e => setPrice(e.target.value)}
              className="w-full bg-neutral-900 border border-neutral-700 rounded-xl p-3.5 text-white focus:border-blue-500 focus:ring-1 focus:ring-blue-500 transition-all outline-none"
            />
          </div>
          <div className="space-y-2">
            <label className="block text-sm font-semibold text-neutral-300">Stok</label>
            <input
              type="number" min="0" value={stock} onChange={e => setStock(parseInt(e.target.value) || 0)}
              className="w-full bg-neutral-900 border border-neutral-700 rounded-xl p-3.5 text-white focus:border-blue-500 transition-all outline-none font-mono"
            />
          </div>
        </div>

        <div className="grid grid-cols-2 gap-4">
          <div className="space-y-2">
            <label className="block text-sm font-semibold text-neutral-300">Urutan Tampil</label>
            <input
              type="number" min="0" value={sortOrder} onChange={e => setSortOrder(parseInt(e.target.value) || 0)}
              className="w-full bg-neutral-900 border border-neutral-700 rounded-xl p-3.5 text-white focus:border-blue-500 transition-all outline-none font-mono"
            />
          </div>
          <div className="space-y-2">
            <label className="block text-sm font-semibold text-neutral-300">Status</label>
            <button
              type="button" onClick={() => setIsActive(!isActive)}
              className={`w-full p-3.5 rounded-xl font-semibold text-sm transition-all border ${isActive ? 'bg-emerald-500/10 text-emerald-400 border-emerald-500/30' : 'bg-neutral-800 text-neutral-400 border-neutral-700'}`}
            >
              <i className={`fa-solid ${isActive ? 'fa-eye' : 'fa-eye-slash'} mr-2`}></i>
              {isActive ? 'Aktif (Tampil)' : 'Nonaktif'}
            </button>
          </div>
        </div>

        <div className="flex gap-3 pt-2">
          <button type="button" onClick={() => router.back()} className="px-6 py-3 rounded-xl bg-neutral-800 hover:bg-neutral-700 font-semibold transition-colors">
            Batal
          </button>
          <button
            type="submit" disabled={saving || uploading}
            className="flex-1 py-3 px-6 bg-blue-500 hover:bg-blue-400 text-neutral-950 font-bold rounded-xl transition-all disabled:opacity-50 shadow-lg shadow-blue-500/20"
          >
            {saving ? <span className="animate-pulse">Menyimpan...</span> : <><i className="fa-solid fa-floppy-disk mr-2"></i>Simpan Perubahan</>}
          </button>
        </div>
      </form>
    </div>
  );
}
